.myModal {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  display: none;
  background: rgba(0, 0, 0, 0.5);
}

.myModalContent {
  padding: 10px 25px;
  background: white;
  border-radius: 9px;
  min-width: 400px;
  border: 1px solid teal;
}

.myModal.active {
  display: flex;
  justify-content: center;
  align-items: center;
}
